import React, { useState } from 'react'
import { Navbar, Nav, NavDropdown, Jumbotron, Button } from 'react-bootstrap';
import './App.css';
import Data from './data'
import Detail from './Detail'
import axios from 'axios';

import {
  Switch,
  Route,
  Link
} from "react-router-dom";
import data from './data';

function App() {

  const [shoes, setShoes] = useState(Data)

  const [stock, setStock] = useState([10,11,12])

  return (
    <div className="App">
      <Navbar bg="light" expand="lg" className="">
        <Navbar.Brand href="#home">Shoe Shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">HOME</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>

        </Navbar.Collapse>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>20% Season Off</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
           </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoes.map((shoe, index) => {
                  return <Product shoe={shoes[index]} filename={index} key={index}></Product>
                })
              }
            </div>
            <button className='btn btn-primary' onClick={() => {

              // axios.post('서버url', {id:'codingapple',pw:'1234'});

              // 로딩중이라는 UI띄움
              axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((result) => { 
                  // 로딩중이라는 UI 안보이게 처리
                  var newShoes = [...shoes,...result.data];
                  setShoes(newShoes);
                })
                .catch(() => {
                  // 로딩중이라는 UI 안보이게 처리
                  console.log('실패했어요')
                })
            }}>더보기 </button>

          </div>
        </Route>
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} setStock={setStock}></Detail>
        </Route>

        <Route path="/:id">
          <div>아무거나 적었을때 이거 보여주셈</div>
        </Route>
        {/* <Route path ="/어쩌구" component={Modal}></Route> */}

      </Switch>
    </div>
  );
}

function Product(props) {

  // var imgPath = "https://codingapple1.github.io/shop/shoes";
  // var imgSrc = imgPath.concat(props.filename, ".jpg");
  return (
    <div className="col-md-4">
      <img src={"https://codingapple1.github.io/shop/shoes" + (props.filename + 1) + ".jpg"} width="100%"></img>
      <h4>{props.shoe.title}</h4>
      <p> {props.shoe.content}</p>
      <p> 가격: {props.shoe.price}</p>
    </div>
  )
}



export default App;
