import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import styled from 'styled-components';
import './Detail.scss'

let 박스 = styled.div`
    padding : 20px;    
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상}
`;

function Detail(props) {

    const [inputData, setinputData] = useState("")
    const [showalert, setshowalert] = useState(true);

    let { id } = useParams();
    let history = useHistory();
    let selectedShoe = props.shoes.find((shoe) => {
        return shoe.id == id
    })
    useEffect(() => {
        let timer = setTimeout(() => {
            setshowalert(false);
        }, 2000)
        return ()=>{
            clearTimeout(timer)
        }
    },[]);

    return (

        <div className="container">
            <박스>
                <제목 색상="blue">Detail</제목>
            </박스>
            {inputData}

            <input onChange={(e)=>{setinputData(e.target.value)}}></input>
            {
                 showalert  === true
                    ? <Myalert></Myalert>
                    : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{selectedShoe.title}</h4>
                    <p>{selectedShoe.content}</p>
                    <p>{selectedShoe.price}</p>
                    <Info stock={props.stock}></Info>
                    <button className="btn btn-danger" onClick={()=>{props.setStock([9,11,12])}}>주문하기</button>
                    <button className="btn btn-danger" onClick={() => {
                        history.goBack();
                    }}>뒤로하기</button>
                </div>
            </div>
        </div>
    )
}

function Myalert() {
    return (
        <div className="my-alert">
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>

    )
}

 function Info(props) {
    return (
        <p> 재고: {props.stock[0]} </p>
    )
}


export default Detail

